import React, { useRef, useState } from 'react';
import List from '@material-ui/core/List';
import AvatarBlock from '../../Components/Avatar';
import clsx from 'clsx';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import CssBaseline from '@material-ui/core/CssBaseline';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import Input from '@material-ui/core/Input';
import SendIcon from '@material-ui/icons/Send';
import Button from '@material-ui/core/Button';
import CardBlock from '../../Components/CardBlock';
const drawerWidth = 240;

const useStyles = makeStyles((theme) => ({

   root: {
      display: 'flex',
   },
   appBar: {
      zIndex: theme.zIndex.drawer + 1,
      transition: theme.transitions.create(['width', 'margin'], {
         easing: theme.transitions.easing.sharp,
         duration: theme.transitions.duration.leavingScreen,
      }),
   },
   appBarShift: {
      marginLeft: drawerWidth,
      width: `calc(100% - ${drawerWidth}px)`,
      transition: theme.transitions.create(['width', 'margin'], {
         easing: theme.transitions.easing.sharp,
         duration: theme.transitions.duration.enteringScreen,
      }),
   },
   menuButton: {
      marginRight: 36,
   },
   hide: {
      display: 'none',
   },
   drawer: {
      width: drawerWidth,
      flexShrink: 0,
      whiteSpace: 'nowrap',
   },
   drawerOpen: {
      width: drawerWidth,
      transition: theme.transitions.create('width', {
         easing: theme.transitions.easing.sharp,
         duration: theme.transitions.duration.enteringScreen,
      }),
   },
   drawerClose: {
      transition: theme.transitions.create('width', {
         easing: theme.transitions.easing.sharp,
         duration: theme.transitions.duration.leavingScreen,
      }),
      overflowX: 'hidden',
      width: theme.spacing(7) + 1,
      [theme.breakpoints.up('sm')]: {
         width: theme.spacing(9) + 1,
      },
   },
   toolbar: {
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'flex-end',
      padding: theme.spacing(0, 1),
      // necessary for content to be below app bar
      ...theme.mixins.toolbar,
   },
   content: {
      flexGrow: 1,
      padding: theme.spacing(3),
   },
}));

export default function Body({ users, client, messages, setMessages }) {
   const date = new Date()
   const message = useRef()
   
   const classes = useStyles();
   const theme = useTheme();
   const [open, setOpen] = React.useState(false);
   const [teleName, setTelename] = useState('Userni tanlang !')


   const handleDrawerOpen = () => {
      setOpen(true);
   };

   const handleDrawerClose = () => {
      setOpen(false);
   };
   console.log(messages);

   return (
      <div className={classes.root}>
         <CssBaseline />
         <AppBar
            position="fixed"
            className={clsx(classes.appBar, {
               [classes.appBarShift]: open,
            })}
         >
            <Toolbar>
               <IconButton
                  color="inherit"
                  aria-label="open drawer"
                  onClick={handleDrawerOpen}
                  edge="start"
                  className={clsx(classes.menuButton, {
                     [classes.hide]: open,
                  })}
               >
                  <MenuIcon />
               </IconButton>
               <Typography variant="h6" noWrap>
                  {teleName}
               </Typography>
            </Toolbar>
         </AppBar>
         <Drawer
            variant="permanent"
            className={clsx(classes.drawer, {
               [classes.drawerOpen]: open,
               [classes.drawerClose]: !open,
            })}
            classes={{
               paper: clsx({
                  [classes.drawerOpen]: open,
                  [classes.drawerClose]: !open,
               }),
            }}
         >
            <div className={classes.toolbar}>
               <IconButton onClick={handleDrawerClose}>
                  {theme.direction === 'rtl' ? <ChevronRightIcon /> : <ChevronLeftIcon />}
               </IconButton>
            </div>
            <Divider />

            <Divider />
            <List>
               {
                  users.length ? (
                     users.map(el => (
                        <AvatarBlock text={el.name} userId={el.userId} setTeleName={setTelename}/>
                     ))
                  ) : (
                     <></>
                  )
               }
            </List>
         </Drawer>
         <main className={classes.content}>
            <div className={classes.toolbar} />

            <div className="row" >

               {
                  messages.length ? (
                     messages.map(el => (
                        <CardBlock
                           name={el.user}
                           time={el.time}
                           text={el.message}
                           isMe={el.user == window.localStorage.getItem('user') ? true : false}
                        />
                     ))
                  ) : (
                     <></>
                  )
               }
            </div>
            <form style={{ display: 'flex', position: 'fixed', bottom: '10px', width: '100%' }}>
               <Input aria-label="minimum height" minRows={20} style={{ width: '75%' }} placeholder="Habaringizni yozing...."
                  inputRef={message}
               />
               <Button
                  variant="contained"
                  color="primary"
                  className={classes.button}
                  endIcon={<SendIcon />}
                  type="submit"
                  onClick={e => {
                        e.preventDefault()
                     if (teleName == 'Userni tanlang !') {
                        alert('Userni tanlang !')
                     }else {
                        client.emit('send_message', {
                           message: message.current.value,
                           userId: window.localStorage.getItem('userName'),
                           userName: window.localStorage.getItem('user')
                        })
                        console.log(messages);
                        setMessages([...messages, {
                           message: message.current.value,
                           user: window.localStorage.getItem('user'),
                           time: date.toLocaleTimeString()
                        }])
                     }
                        
                     {
                        
            }
                        // setMessage([...message, {
                        //    ab: 'm',
                        //    message: e.target.value
                        // }])
                     
                  }}
               >
                  Send
               </Button>
            </form>
         </main>
      </div>
   );
}
