import IO from 'socket.io-client'
import {useEffect, useState} from 'react'


import './main.css'
import SignIn from './containers/Sign/Sign'
import Body  from './containers/Body/Body'


function App() {
  const [client, setClient] = useState(null)
  const [users, setUsers] = useState([])
  const [token, setToken] = useState(false)
  const [myName, setMyName] = useState('')
  const [messages, setMessages] = useState([])

 

  useEffect(()=> {
    setClient(IO('http://192.168.1.253:4200', {
      transports: ['websocket']
    }))
  }, [])



  useEffect(() => {
    if (client)  {


      client.on("send_message", data => {
        setMessages([...messages, ...data])
      });
      client.on("message", data => {
        let array = []
        for (let key in data) {
          if (key != window.localStorage.getItem('user'))
            array.push({ name: key, userId: data[key] })
        }
          
        
        setUsers(array)
      });
      client.on('men_keldim', data => {
        let array = []
        for(let key in data) {
          if(key != window.localStorage.getItem('user'))
            array.push({name: key, userId: data[key]})
          }
        
        setUsers(array)
        // setUsers([data])
      })
      // client.on('typing', () => setTyping(true))
      // client.on('send', data => {
      //   setMessage([...message, {
      //     ab: 'u',
      //     message: data
      //   }])
      //   setTyping(false)
      // })
    }
  }, [client, messages])



  return (
    <div className="App">
      {/* <input 
        type="text"
        placeholder="Name"
        spellCheck={false}
        autoComplete='off'
        onKeyUp={ e => {
          if(e.keyCode === 13) {
            client.emit('come', e.target.value)
            setMessage([...message, {
              ab: 'm',
              message: e.target.value}])
          }else {
            client.emit('typing')
          }
        }}
        
       /> */}\

      {
        token ? (
          <Body users={users} client={client} messages={messages} setMessages={setMessages}/>
          ) : (
            <SignIn setToken={setToken} client={client} setMyName={setMyName} />
            )
      }

       
    </div>
  );
}

export default App;
