import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import './main.scss'

const useStyles = makeStyles({
   root: {
      minWidth: 275,
   },
   bullet: {
      display: 'inline-block',
      margin: '0 2px',
      transform: 'scale(0.8)',
   },
   title: {
      fontSize: 14,
   },
   pos: {
      marginBottom: 12,
   },
});

export default function CardBlock({name, time, text, isMe}) {
   const classes = useStyles();
   const bull = <span className={classes.bullet}>â€¢</span>;

   return (
      <div className={`cardBlock ${isMe ? 'active' : ''}`}>
         <Card className={`${classes.root} cardBlock-inner`} variant="outlined" >
            <CardContent>
               <div className="row" style={{ display: 'flex', alignItems: "center", justifyContent: "space-between" }}>
                  <Typography component="h6" variant="h6" color="textSecondary" gutterBottom style={{ fontWeight: 900 }}>
                     {name}
                  </Typography>
                  <Typography component="h6" variant="h6" color="textSecondary" gutterBottom style={{ background: "#3F51B5", color: '#fff', padding: '0px 5px', borderRadius: '5px', fontSize: '17px' }}>
                     {time}
                  </Typography>
               </div>


               <Typography variant="body2" component="p">
                  {text}
               </Typography>
            </CardContent>
         </Card>
      </div>
   );
}
